import React, { useState, useEffect } from 'react';
import App, { Container } from 'next/app';


function Airtable() {
  var Airtable = require('airtable');
  const api_base = new Airtable({apiKey: 'keyBCQBasy6oWdP63'}).base('app3H9oIfok6KQvG5');
  const [products, setProducts] = useState([]);

  useEffect(() => {
    var fetched_products = [];
    api_base('Products').select({
      // Selecting the first 3 records in Grid view:
      maxRecords: 30,
      view: "Grid view"
    }).eachPage(function page(records, fetchNextPage) {
      // This function (`page`) will get called for each page of records.
      records.forEach(function(record) {
        fetched_products.push(record);
      });

      // To fetch the next page of records, call `fetchNextPage`.
      // If there are more records, `page` will get called again.
      // If there are no more records, `done` will get called.
      fetchNextPage();

    }, function done(err) {
      if (err) { console.error(err); return; }
      setProducts(fetched_products);
    });
  });

  return (
    <ul>
      {products.map(item => (
        <li key={item.get('Name')}>
          <h3>{item.get('Briefing')}</h3>
          {item.get('Name')}
        </li>
      ))}
    </ul>
  );
}

export default Airtable;
